//// *******************************
////  LAYOUT
//// *******************************
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: $theme-font;
}

.page-footer {
  .btn-container {
    display: flex;
    justify-content: space-between;

    @media #{$small-and-down} {
      display: block;
    }
  }

  .btn {
    font-size: 0.7rem;
    margin: 1rem 0;
  }
}

.menu-title {
  // This is recommanded by materialize.css: http://materializecss.com/color.html
  // scss-lint:disable PlaceholderInExtend
  @extend .red, .darken-4;
  // scss-lint:enable PlaceholderInExtend
}

.footer-copyright .container {
  align-items: center;
  display: flex;
  justify-content: space-between;
}



